export default {
	name: "",
	components: {

	},
	props: {},
	data() {
		return {
			username: '', //用户账号
			password: '', //,密码
			Openid: '', //令牌
		}
	},
	methods: {
		//点击登录 普通登录
		tologin() {
			console.log(this.username, this.password)
			uni.request({
				url: `${this.$api}/user/registOrLogin?qq=122212489`,
				method: 'POST',
				data: {
					userBO: {}, //用户对象	
					username: this.username, //用户账号
					password: this.password, //,密码
				},
				success: res => {
					if (res.statusCode === 200) {
						uni.showToast({
							title: '登录成功',
							duration: 2000
						})
						uni.switchTab({
							url: "/pages/min/min"
						})
						this.OpenId = res.data.data.userUniqueToken;
						uni.setStorageSync('OpenId', this.OpenId)
						uni.setStorageSync("userBO", res.data.data);
						
						// console.log(this.OpenId)
						// console.log(res.data.data.userUniqueToken)
						// console.log(uni.getStorageSync('userBO'))					
						// uni.setStorageSync("OpenId",this.OpenId)
					}
					// console.log(res)
				},
				fail: () => {},
				complete: () => {}
			});
			// 
		},
		inputVal(e) { //获取账号
			this.username = e.detail.value;
			// this.userBO.username = e.detail.value;
			// console.log(this.username)
		},
		passwordVal(e) { //获取密码
			this.password = e.detail.value;
			// this.userBO.password = e.detail.value;
			// console.log(this.password)
		},
		wxlogin() { //第三方微信登录
			uni.login({
				provider: 'weixin',
				success: (res) => {
					//获取用户信息
					uni.getUserInfo({
						provider: 'weixin',
						success: function(infoRes) {
							let openId = infoRes.userInfo.openId;
							uni.setStorageSync('openId');
							uni.setStorageSync('userBO',infoRes.userInfo);
							uni.showToast({
								title: '登录成功',
								duration: 2000
							})
							uni.switchTab({
								url: "/pages/min/min"
							})
							// console.log('用户昵称为：' + infoRes.userInfo.nickName);
							console.log(infoRes.userInfo)
						}
					})
					// console.log(res);
				}
			})
		},
		qqlogin() {
			uni.login({
				provider: 'qq',
				success: function(loginRes) {
					console.log(loginRes.authResult);
					uni.getUserInfo({
						provider: 'qq',
						success: function(infoRes) {
							let openId = infoRes.userInfo.openId;
							uni.setStorageSync('openId');
							uni.setStorageSync('userBO',infoRes.userInfo);
							uni.showToast({
								title: '登录成功',
								duration: 2000
							})
							uni.switchTab({
								url: "/pages/min/min"
							})
							// console.log('用户昵称为：' + infoRes.userInfo.nickName);
							console.log(infoRes.userInfo)
						}
					})
				}
			});
		},
		// wblogin(){
		// 	uni.login({
		// 	  provider: 'wb',
		// 	  success: function (loginRes) {
		// 	    console.log(loginRes.authResult);
		// 	  }
		// 	});
		// },
	},
	mounted() {

	},
	onLoad() {

	},
	filters: {

	},
	computed: {

	},
	watch: {

	},
	directives: {

	}
}
